import React, {
	useState,
	useEffect
} from 'react'
import {
	Link
} from 'react-router-dom'
import {
	createMuiTheme
} from '@material-ui/core/styles'
import {
	ThemeProvider
} from '@material-ui/styles'
import Button from '@material-ui/core/Button'
import {
	purple
} from '@material-ui/core/colors'
import './index.scss'


let [current, bool] = [0, true]

const getElement = () => {
	let box = document.querySelector('.home-ul')
	let ul = document.querySelector('.home-ul ul')
	return [box, ul]
}

const changePage = (setIndex, box, ul) => {
	box.onmousewheel = e => {
		ul.style.transition = 'all .5s ease-out'
		if (bool) {
			const h = box.offsetHeight
			if (e.wheelDelta < 0 && current < ul.children.length - 1) {
				current += 1
				ul.style.transform = `translateY(-${ current * h}px)`
				bool = false
				setTimeout(() => {
					bool = true
				}, 300)
			}
			if (e.wheelDelta > 0 && current > 0) {
				current -= 1
				ul.style.transform = `translateY(-${ current * h}px)`
				bool = false
				setTimeout(() => {
					bool = true
				}, 300)
			}
		}
		setIndex(current)
	}
}

const update = (box, ul) => {
	window.onresize = () => {
		const h = box.offsetHeight
		ul.style.transition = 'none'
		ul.style.transform = `translateY(-${ current * h}px)`
	}
}

const theme = createMuiTheme({
	palette: {
		primary: purple
	}
})

const Home = props => {
	const [index, setIndex] = useState(0)

	useEffect(() => {
		changePage(setIndex, ...getElement())
		update(...getElement())
	}, [])

	return <section id='home'>
		<div className='home-ul'>
			<ul>
				{
					[0, 1, 2].map(item => {
						return <li key={ item }></li>
					})
				}
			</ul>
			<ol>
				{
					[0, 1, 2].map(item => {
						return <li key={ item } className={index===item?'current':''}></li>
					})
				}
			</ol>
			<Link to={'/login'} className='home-login'>
				<ThemeProvider theme={theme}>
			        <Button variant="contained" color="primary">
						Login Now
			        </Button>
			    </ThemeProvider>
		    </Link>
		</div>
 	</section>
}


export default Home